* {
  box-sizing: border-box;
  background-color: black;
  color: white;
}

#movie-card-card {
  margin-top: 35px;
  min-height: 33rem;
  max-height: 33rem;
}

#movie-card-col {
  width: 200px;
  min-height: 300px;
  height: 100%;
  object-fit: cover;
}

#movie-card-image {
  max-width: 100%;
  height: 375px;
  object-fit: cover;
}